import { Heading } from '@chakra-ui/react';
import React, { useEffect } from 'react'
import style from './Skills.module.css'
import Aos from "aos"
import "aos/dist/aos.css"

export default function Skills({ colorMode }) {

    const skillsArray = [
        { id: 1, name: "HTML", logo: "https://us.123rf.com/450wm/innayatsuk/innayatsuk2102/innayatsuk210200051/165304530-orange-3d-html5-icon-isolated-on-white-background-.jpg?ver=6" },
        { id: 2, name: "CSS", logo: "https://ih1.redbubble.net/image.559137225.8320/raf,750x1000,075,t,FFFFFF:97ab1c12de.jpg" },
        { id: 3, name: "JavaScript", logo: "https://ih1.redbubble.net/image.1091999270.7282/flat,750x,075,f-pad,750x1000,f8f8f8.jpg" },
        { id: 4, name: "React", logo: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQI0RAllozh2bKCwPOYa6fqlTm7nSkGG_rFvQ1PKbMyOFqbySD76m6wslx1zryADjqgj_E&usqp=CAU" },
        { id: 5, name: "Redux", logo: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRNe3NYVlI0lgrZLMto86XvqTSkxKEs6XRF9JmjFA-XSH5j0QtWclwj-5NwP7z4CbYlNMQ&usqp=CAU" },
        { id: 6, name: "MongoDB", logo: "https://newrelic.com/sites/default/files/styles/800w/public/2021-10/mongo_logo.jpg?itok=Z1PabBZB" },
        { id: 7, name: "Express", logo: "https://www.vectorlogo.zone/logos/expressjs/expressjs-ar21.png" },
        { id: 8, name: "NodeJS", logo: "data:image/png;base64," },
        { id: 9, name: "Chakra UI", logo: "data:image/png;base64," },
        { id: 10, name: "BootStrap", logo: "https://cdn.dribbble.com/users/595/screenshots/14417863/bootstrap-new-logo_4x.png" },
        { id: 11, name: "Animation", logo: "https://animate.style/img/animatecss-opengraph.jpg" },
        { id: 12, name: "Webpack", logo: "https://ih1.redbubble.net/image.393391389.2519/flat,750x1000,075,f.u1.jpg" },
    ];

    useEffect(() => {
        Aos.init()
    }, [])

    return (
        <div>
            <div id='Skills' style={{ height: "100px" }}></div>
            <div id={style.skill}>
            <h1 style={{color:"transparent"}}>ABOUT ME</h1>
                <Heading size={['2xl', '2xl', '2xl', '3xl']} textAlign='center'  fontSize="70px" color={colorMode === "whiteSmoke"}>SKILLS</Heading>
                <div id={style.skillDiv}>
                    {skillsArray.map((skill) => (
                        <div key={skill.name} className={style.skill_in_div} >
                            <div data-aos="zoom-in">
                                <img className={style.logo} src={skill.logo} alt={skill.name} />
                            </div>
                            <h3 className={style.skillH3} style={{ color: colorMode === 'whiteSmoke' ? "whiteSmoke" : 'whiteSmoke' }}>{skill.name}</h3>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}
